body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
  color: #000;
  background: #ebebeb; }

nav {
  border-bottom: inset black 1px; }
  nav a:hover {
    background-color: lightgrey; }

@media (min-width: 445px) {
  .nav {
    margin-bottom: 25px; } }

body {
  background: #ebebeb; }

h5 {
  font-weight: bold; }

.profile {
  float: left;
  max-width: 100%;
  margin-right: 20px; }
  @media (min-width: 600px) {
    .profile {
      max-width: 150px; } }

@media (max-width: 480px) {
  .about {
    clear: both;
    display: inline-block; }
  .profile {
    margin-left: auto;
    margin-right: auto;
    display: block;
    float: none; } }

footer {
  padding-bottom: 20px; }
  footer a img {
    width: 40px;
    height: 40px;
    float: right; }
    footer a img:hover {
      filter: contrast(50%);
      -webkit-filter: contrast(50%); }

code {
  background: darkslategray;
  max-height: 400px; }

.btn-link {
  font-weight: bold;
  height: 100%;
  width: 100%;
  text-align: left;
  padding: 1rem; }

.foot-text {
  text-align: center;
  vertical-align: middle;
  font-family: italic;
  color: grey;
  margin-top: 8px;
  margin-bottom: 8px; }
  @media (max-width: 445px) {
    .foot-text {
      margin-top: -4px; } }

/* Specific rule for mobile footer */
@media (max-width: 600px) {
  footer .container .row .col {
    padding-left: 0px;
    padding-right: 0px; } }

.card-header {
  padding: 0; }


.project-card {
  -webkit-flex-direction: column;
          flex-direction: column;
  padding-top: 20px;
  margin-top: 30px;
  border-top: solid 1px black; }
  .project-card p {
    margin-bottom: 0; }

.project-card, .project-header, .project-main {
  display: -webkit-inline-flex;
  display: inline-flex;
  width: 100%; }

.project-header {
  display: block; }
  @media screen and (min-width: 768px) {
    .project-header {
      display: -webkit-inline-flex;
      display: inline-flex;
      -webkit-flex-wrap: wrap;
              flex-wrap: wrap; } }
  .project-header hr {
    width: 100%;
    margin: 1px 0; }

.project-img {
  padding: 0; }

.project-img, .project-img img {
  width: 100px;
  height: 100px; }
  @media screen and (min-width: 768px) {
    .project-img, .project-img img {
      width: 150px;
      height: 150px; } }

.project-body {
  width: 100%;
  padding-left: 5px;
  padding-right: 5px; }
  @media screen and (min-width: 768px) {
    .project-body {
      padding-left: 20px; } }

.project-title {
  -webkit-flex: 1 1 auto;
          flex: 1 1 auto; }
  .project-title h4 {
    text-align: left;
    margin-bottom: 0; }
  .project-title .project-job {
    font-style: italic;
    margin-top: -5px;
    font-size: 0.95em; }

.project-dates {
  font-style: italic;
  vertical-align: bottom;
  margin-bottom: auto; }
  @media screen and (min-width: width 768px) {
    .project-dates {
      margin-top: 5px; } }

.project-description p, .project-description-mobile p {
  font-size: 1em;
  margin-bottom: 0px; }

.project-description ul, .project-description-mobile ul {
  list-style-type: square; }

@media screen and (max-width: 768px) {
  .project-description {
    display: none; } }

.project-description-mobile {
  display: none; }
  @media screen and (max-width: 768px) {
    .project-description-mobile {
      display: block; } }

.project-footer {
  margin-top: 10px;
  display: -webkit-inline-flex;
  display: inline-flex; }
  .project-footer .project-used {
    width: 100%;
    margin: auto;
    display: -webkit-inline-flex;
    display: inline-flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap; }
  .project-footer .project-link {
    -webkit-flex: 1 1 auto;
            flex: 1 1 auto; }

.project-language {
  border-radius: 7px;
  border: solid 1px  #aaa;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 0 4px;
  font-size: 0.8em;
  margin-right: 5px; }

.grain {
  background: url(/static/media/noise.651b1f99.jpg);
  background-size: auto;
  background-size: cover;
  position: absolute;
  width: inherit;
  height: inherit;
  z-index: 99;
  opacity: 0.1;
  transition: ease 0.2s;
  pointer-events: none; }

.contact-form {
  display: -webkit-inline-flex;
  display: inline-flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 100%;
  max-width: 500px; }

.contact {
  display: block; }

.main-test img {
  width: 100px;
  height: 100px;
  float: left; }

.main-test .test-header {
  width: calc(90%);
  display: -webkit-inline-flex;
  display: inline-flex; }

.main-test .test-title {
  display: -webkit-inline-flex;
  display: inline-flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  margin-right: auto; }
  .main-test .test-title * {
    padding: 0;
    margin: 0; }

